import { Link } from "react-router-dom";

import twitter from '../../assets/twitter.svg';
import discord from '../../assets/discord.svg';

export const Twitter = () => (
  <Link className="text-invar-purple hover:underline z-40"
    to='#'
    onClick={(e) => {
      window.open('https://twitter.com/InVarFinance');
      e.preventDefault();
    }}
  >
    <img className="rounded w-10" src={twitter} alt="Logo" />
  </Link>
);

export const Discord = () => (
  <Link className="text-invar-purple hover:underline z-40"
    to='#'
    onClick={(e) => {
      window.open('https://discord.gg/BrzPWYut4p')
      e.preventDefault();
    }}
  >
    <img className="rounded w-10" src={discord} alt="Logo" />
  </Link>
);

export const OpenLink = ({ link, children } : { link: string, children: any }) => (
  <Link className="text-invar-purple hover:underline z-40"
    to='#'
    onClick={(e) => {
      window.open(link)
      e.preventDefault();
    }}
  >
    {children}
  </Link>
);

export const ButtonMailto = () => (
  <Link className="text-invar-purple hover:underline"
    to='#'
    onClick={(e) => {
      window.location.href = 'mailto:info@invar.finance';
      e.preventDefault();
    }}
  >
    {`info@invar.finance`}
  </Link>
);